<template>
  <div class="container">
    <div class="form-box">
      <el-form ref="form" :model="form"  label-width="160px">
<!--        <el-form-item label="服务名称" prop="serviceTypeName" :label-width="formLabelWidth">-->
<!--          <el-input v-model="form.serviceTypeName" autocomplete="off" class="mz_wb"></el-input>-->
<!--        </el-form-item>-->
<!--        <el-form-item label="价格" prop="serviceTypePrice" :label-width="formLabelWidth">-->
<!--          <el-input v-model="form.serviceTypePrice" autocomplete="off" class="mz_wb"></el-input>-->
<!--        </el-form-item>-->
        <el-form-item label="图片" class="padding" prop="img" :label-width="formLabelWidth">
          <el-upload
            class="avatar-uploader"
            :action="uploadUrl"
            name="image"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="img+imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item class="submit">
          <el-button type="primary" @click="onSubmit('form')">提交</el-button>
          <el-button @click="$router.go(-1)">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>

</template>

<script>
  import http from '../../../../assets/js/http'

  export default {
    name: 'bannarLINdex',
    data() {
      return {
        cur_page: 1,
        tableData: [],
        total: 0,
        cur_size:10,
        multipleSelection: [],
        select_cate: '',
        select_word: '',
        del_list: [],
        is_search: false,
        delVisible: false,
        delVisible1: false,
        index:"",
        valid:1,
        img:window.HOST+'/',
        uploadUrl:window.HOST+'/admin/upfile',
        imageUrl:'',
        dialogFormVisible:false,	//上传图片
        form:{
          img:'',
          remark:'',
          id:'',
        },
        formLabelWidth: '120px',
      }
    },
    created() {
      this.getData();
    },
    methods: {
      // 获取 easy-mock 的模拟数据
      getData() {
        var that = this;
        let param = {
          status:0,
          datoken:1
        };
        this.apiPost('/admin/djlbone', param).then((res) => {
          that.handelResponse(res,(data)=>{
            that.tableData = res.data.data;
            that.imageUrl = that.tableData.src
          })
        })
      },
      onSubmit(){
        var that = this;
        let param = {
          src:that.imageUrl,
          datoken:that.tableData.datoken,
          // minsrc:that.imageUrl,
          // ctoken:that.tableData.ctoken,
          // status:0
        };
        this.apiPost('/admin/djlbupdate', param).then((res) => {
          that.handelResponse(res,(data)=>{
            that.$message.success('修改成功');
            that.getData()
          })
        })
      },
      //上传图片
      handleAvatarSuccess(res, file) {
        this.imageUrl = res.data;
        this.form.img=res.data;
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;
      },
    },
    mixins: [http]
  }
</script>

<style>
  img{
    width: 100%;
    height: 100%;
  }
  .table .el-upload--text {
    width: 178px;
  }
</style>



